import React, { Component } from 'react';
import { Select, MenuItem, Snackbar, IconButton } from '@material-ui/core';
import { Close } from '@material-ui/icons';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import './styles/Navbar.css';

export default class Navbar extends Component {
  state = {
    format: this.props.format,
    seed: this.props.seed,
    open: false,
  };

  handleFormatChange = (evt) => {
    this.setState({
      format: evt.target.value,
      open: true,
    });
    this.props.changeFormat(evt.target.value);
  };
  handleSeedChange = (evt) => {
    this.setState({
      seed: evt.target.value,
    });
    this.props.changeSeed(evt.target.value);
  };
  closeSnackbar = () => {
    this.setState({
      open: false,
    });
  };
  render() {
    const { format, seed, open } = this.state;
    const { level, changeLevel } = this.props;
    return (
      <header className="Navbar">
        <div className="logo">
          <a href="/">React Color Picker</a>
        </div>
        <div className="slider-container">
          <span>Level: {level}</span>
          <div className="slider">
            <Slider
              defaultValue={level}
              min={100}
              max={900}
              step={100}
              onAfterChange={changeLevel}
            />
          </div>
        </div>
        <div className="select-container">
          <Select
            className="seed-selector"
            value={seed}
            onChange={this.handleSeedChange}
          >
            <MenuItem value={0}>Material UI Colors</MenuItem>
            <MenuItem value={1}>Flat UI Colors v1</MenuItem>
            <MenuItem value={2}>Flat UI Colors Dutch</MenuItem>
            <MenuItem value={3}>Flat UI Colors American</MenuItem>
            <MenuItem value={4}>Flat UI Colors Aussie</MenuItem>
            <MenuItem value={5}>Flat UI Colors British</MenuItem>
            <MenuItem value={6}>Flat UI Colors Spanish</MenuItem>
            <MenuItem value={7}>Flat UI Colors Indian</MenuItem>
            <MenuItem value={8}>Flat UI Colors French</MenuItem>
          </Select>
          <Select
            className="format-selector"
            value={format}
            onChange={this.handleFormatChange}
          >
            <MenuItem value="hex">HEX - #ffffff</MenuItem>
            <MenuItem value="rgb">RGB - (255, 255, 255)</MenuItem>
            <MenuItem value="rgba">RGBA - (255, 255, 255, 1)</MenuItem>
          </Select>
        </div>
        <Snackbar
          anchorOrigin={{ vertical: 'bottom', horizontal: 'left' }}
          open={open}
          autoHideDuration={6000}
          onClose={this.closeSnackbar}
          message={
            <span id="message-id">Format change to {format.toUpperCase()}</span>
          }
          ContentProps={{
            'aria-describedby': 'message-id',
          }}
          action={
            <React.Fragment>
              <IconButton
                key="close"
                aria-label="close"
                color="inherit"
                onClick={this.closeSnackbar}
              >
                <Close />
              </IconButton>
            </React.Fragment>
          }
        />
      </header>
    );
  }
}
